@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Cute+Font|Inconsolata);
body {
  background: #161a1e;
  font-family: 'Amatic SC', cursive;
  font-size: 140%;
  padding: 20px;
}

.error{
  color:red;
  font-size: 20px;
  font-weight: bold;
}

.login {
  margin: auto;
  width: 400px;
  padding-top: 140px;
}

h1 {
  color: #fc2847;
}

.web {
  font-family: 'Cute Font', cursive;
}

p {
  font-size: 28px;
  color: #ff033e;
  font-weight: 600;
}

input {
  outline: none;
  border: none;
  border-radius: 4px;
  height: 21px;
  width: 400px;
  background: #161a1e;
  font-size: 23px;
  padding: 3px;
  color: red;
  font-family: 'Amatic SC', cursive;
}

input::-webkit-input-placeholder {
  color: rgba(255, 64, 64, 0.9);
}

input::-ms-input-placeholder {
  color: rgba(255, 64, 64, 0.9);
}

input::-moz-placeholder {
  color: rgba(255, 64, 64, 0.9);
}

input::placeholder {
  color: rgba(255, 64, 64, 0.9);
}

.btn {
  margin-right: 12px;
}

.participants {
  color: white;
  margin-right: 10px;
}


video {
  width: 300px;
}

.chatRoom {
  display: flex;
  justify-content: space-between;
}

.chatWindow {
  margin-top: 20px;
  height: 400px;
  overflow-y: auto;
  position: relative;
}



.chatWindow li {
  list-style: none;
  color: white;
}

::-webkit-scrollbar-button {
background-image:url('');
background-repeat:no-repeat;
width:5px;
height:0px
}

::-webkit-scrollbar-track {
background-color:#ecedee
}

::-webkit-scrollbar-thumb {
border-radius: 0px;
background-color:#ff033e;
}

::-webkit-scrollbar-thumb:hover{
background-color:#ff033e;
}

::-webkit-resizer{
background-image:url('');
background-repeat:no-repeat;
width:4px;
height:0px
}

::-webkit-scrollbar{
width: 4px;
}
